import { Component, OnInit ,Input ,Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-payment-applepay',
  templateUrl: './payment-applepay.component.html',
  styleUrls: ['./payment-applepay.component.scss']
})
export class PaymentApplepayComponent implements OnInit {

  @Output() payEmitter:EventEmitter<any> = new EventEmitter<any>();

  constructor() { }

  ngOnInit(): void {
    this.payEmitter.emit(true);
  }

}
